import React from 'react';
import './OnlineEducation.css';
import img from '../images/card-2-background.png';

const OnlineEducation = () => {
  return (
    <>
      <div className="online-education-top-wrapper">
        <div className="online-education-right-element">
          <h4>التعليم عن بعد</h4>
          <p>
            تعلَّم المفاهيم الجديدة والتقنيات المعاصرة وادخل عالم التعليم الرقمي
            دون تردد
            <br />
            التعليم عن بُعد هو باختصار مستقبل التعليم، هل تبحث عن من يرشدك
            لاكتشاف هذا العالم ويُقدم الدعم اللازم لك ولمؤسستك التعليمية
            والمعلمين والطلبة وأولياء الأمور؟ نحن نمنحك منظور جديد وأفكار
            ابداعية لتتميز في هذا المجال، من خلال ورش العمل والاستشارات التي
            نقدمها: التعليم عن بُعد المفهوم الجديد والتقنية المعاصرة. مشرف
            تقنيات التعليم ودوره في دعم التحول الرقمي للمؤسسات التعليمية.
            الحقائب التدريبية. Distance Learning; a skillset or a mindset?
            للمشاركة تفضل بالتسجيل.
          </p>
        </div>
        <div className="online-education-left-element">
          <img src={img} alt="" width="100%" height="100%" />
        </div>
      </div>
      <div className="online-education-bottom-element">
        <div className="row">
          <div className="input-field col s6">
            <textarea
              id="textarea1"
              className="materialize-textarea direction-rtl"
              placeholder="الطلب"
            ></textarea>
          </div>
          <div className="input-field col offset-s1 s4">
            <input
              placeholder="الاسم"
              type="text"
              className="validate direction-rtl"
            />
            <input
              placeholder="البريد الإلكتروني"
              type="email"
              className="validate direction-rtl"
            />
          </div>
        </div>
        <button className="waves-effect waves-light btn-large blue lighten-3">
          إرسال
        </button>
      </div>
    </>
  );
};

export default OnlineEducation;
